<template>
  <div class="">
    <topbar :title="currDate" :showback="false"></topbar>
    <div class="home-btns clearfix mt20">
      <button class="home-btn home-btns__prev fl" v-bind:disabled="curr==0" @click="curr--"></button>
      <button class="home-btn home-btns__next fr" v-bind:disabled="curr==kneels.length-1" @click="curr++"></button>
    </div>
    <swiper :index.sync="curr" height="400px" :show-dots="false" >
      <swiper-item v-for="kneel in kneels" track-by="$index">
        <div class="circle" v-bind:style="{ width: circleWidth +'px', lineHeight: circleWidth +'px', height: circleWidth +'px' }" >
          {{kneel.kneelCount}}<span></span>
        </div>
      </swiper-item>
      <div class="circle circle-blur circle-blur__l" v-bind:style="{width: circleWidth +'px', height: circleWidth +'px', left: blurPosition + 'px'}" v-show="curr!=0"></div>

      <div class="circle circle-blur circle-blur__r" v-bind:style="{width: circleWidth +'px', height: circleWidth +'px',right: blurPosition + 'px'}" v-show="curr!=kneels.length-1"></div>
    </swiper>

  </div>

</template>

<script>
import store from '../vuex/store';
import Topbar from './Topbar';
import {Swiper, SwiperItem} from 'vux/src/components';
import moment from 'moment';
export default {
  components: {
    Swiper,
    SwiperItem,
    Topbar
  },
  store:store,
  vuex:{
    getters:{
      kneels: (state) => state.kneels
    }
  },
  data(){
    return {
      curr: 0,
      circleWidth:240,
      blurPosition:-240,
    }
  },
  created(){
    moment.locale('zh-cn');

    if(this.kneels.length==0){
      this.getKneelsFromServer();
    }else{
      this.curr = this.kneels.length-1;
    }

    this.initCircle()
  },
  computed:{
    /**
     * [currDate ]
     * @return {[string]}
     */
    currDate(){
      var datetime = this.kneels.length!=0?moment(this.kneels[this.curr].startTime).format('MMM Do dddd'):'';
      return datetime;
    }
  },
  methods:{
    /**
     * [initCircle ]
     * @return {[type]}
     */
    initCircle(){
      this.circleWidth = document.body.clientWidth * 74 / 100;
      this.blurPosition =  (-1 * this.circleWidth) + 10;
    },
    /**
     * [getKneelsFromServer ]
     * @return {[type]} [description]
     */
    getKneelsFromServer(){
      let now = Date.parse(new Date()) + 1000 * 24 * 60 * 60;
      /*30*/
      let begin = moment(now - 30 * 1000 * 24 * 60 * 60).format('YYYY-MM-DD');
      let nonKneel = [{
        kneelCount: 0,
        startTime: now
      }]
      this.$http.post('wx/kneels',{openId:window.OPENID,beginTime:begin,endTime: moment(now).format('YYYY-MM-DD')}).then((res)=>{
        /*0*/

        if(res.body.success){
          if(res.body.data.result.length==0){
            store.dispatch('UPDATE_KNEELS', nonKneel);
          }else{
            store.dispatch('UPDATE_KNEELS', res.body.data.result.reverse());
            this.curr = this.kneels.length-1;
          }
        }else{
          store.dispatch('UPDATE_KNEELS', nonKneel);
        }
      },(response) => {
        // error callback
        store.dispatch('UPDATE_KNEELS', nonKneel);
      })
    }
  }
}
</script>

<style lang="scss">
$circle-width: 240px ;
.circle{
  background: #EABF20;
  height: $circle-width;
  width: $circle-width;
  line-height: $circle-width;
  color: #fff;
  font-size: 70px;
  border-radius: 50%;
  text-align: center;
  margin: 0 auto;
  position:relative;
  span{
    position:absolute;
    line-height:normal;
    color:#fff;
    bottom:20px;
    width:100%;
    display:inline-block;
    left:0;
    font-size:18px;
    &::before{
      content:'';
      width:60%;
      height:1px;
      background:#DFB100;
      display:block;
      margin:0 auto;
      position:absolute;
      margin-top:-15px;
      left:20%;
    }
  }
}
.circle-blur{
  background:#ECD787;
  opacity:.7;
  box-shadow: 0 0 30px #EABF20;
  position: absolute;
  top:0;
  &.circle-blur__l{
    left: -70%;
  }
  &.circle-blur__r{
    right: -70%;
  }
}
.home-btns{
  padding:15px;
  button{
    font-size:16px;
    color:#444;
    border:none;
    background: none;
  }
  button[disabled]{
    color:#ccc
  }
}
</style>



