<template>
  <div class="top-bar">
    <span @click="backHistory" class="btn-back" v-if="showback"><img src="../assets/icon_top_back.png" alt="" /></span>
    <h4>{{title}}</h4>
  </div>
</template>
<script>
  export default {
    props:[
      {
        name:'title',
        type:String
      },
      {
        name:'showback',
        type: Boolean,
        default: true
      }
    ],
    methods:{
      backHistory(){
        window.history.back();
      }
    }
  }
</script>
<style lang="scss">
  .top-bar{
    height: 44px;
    line-height: 44px;
    position: relative;
    .btn-back{
      width:30px;
      height: 30px;
      display: inline-block;
      text-align: center;
      margin-top: 5px;
      img{
        height: 21px;
      }
    }
    h4{
      position: absolute;
      z-index: -1;
      top:0;
      font-size: 18px;
      color: rgb(80, 80, 80);
      font-weight: normal;
      text-align: center;
      width: 100%;
    }
  }

</style>



