<template>
  <div id="tophead">
    <section class="ranking-header">
      <span @click="loadRanking" class="f12"></span>
      <h4></h4>
      <div class="dropdown pull-right f12">
        <select v-model="province" v-on:change="loadRanking">
          <option value="" selected></option>
          <option value={{user.province}} >{{user.province}}</option>
        </select>
      </div>
    </section>
  </div>
  <section class="ranking-list myself" id="myRank">
    <div class="row">
      <div class="col col-3 ranking-avatar" style="margin-left:15px;text-align:center">
        <img :src="user.headimgUrl" class="circle-avatar" v-if="user.headimgUrl" />
        <img src="../assets/default_avatar.png" class="circle-avatar" v-else/>
      </div>
      <div class="col col-3 ranking-name f16">
        {{user.nickName}}
        <div class="f14">
         {{myRank.ranking}}
        </div>
      </div>
      <div class="col col-3 ranking-step">
        {{myRank.kneelCount}}
      </div>
    </div>
  </section>
  <section id="rankingList">
    <scroller lock-x scrollbar-y :height="scrollerH + 'px'" :prevent-default="false" v-ref:scroller>
      <section class="ranking-list">
        <div class="row" v-for='people in rankinglist'>
          <div class="col ranking-num f22">
            {{people.ranking}}
          </div>
          <div class="col ranking-avatar">
            <img :src="people.headimgUrl" class="circle-avatar" v-if="people.headimgUrl" />
            <img src="../assets/default_avatar.png" class="circle-avatar" v-else/>
          </div>
          <div class="col ranking-name f16">
            {{people.nickName}}
          </div>
          <div class="col ranking-step">
            {{people.kneelCount}} <span></span>
          </div>
        </div>
      </section>
    </scroller>
  </section>

  </div>
</template>

<script>
import {Scroller,ViewBox} from 'vux/src/components'
export default {
  components: {
    Scroller,
    ViewBox
  },
  vuex: {
    getters: {
      user: (state) => state.user
    }
  },
  data: function () {
    return {
      rankinglist: [],
      myRank:{},
      province:'',
      favnum: 0,
      loaded: false,
      scrollerH:200
    }
  },
  ready: function () {
    this.loadRanking();
    this.scrollerH = this.scrollH()
  },
  methods: {
    scrollH(){
      let viewH = document.body.clientHeight;
      let headH = document.getElementById("tophead").clientHeight;
      let myRankH = document.getElementById("myRank").clientHeight;
      let scrollerH = viewH - headH - myRankH - 55 - 15;
      return scrollerH;
    },
    /**
     * [loadRanking ]
     * @return {[type]} [description]
     */
    loadRanking(){
      this.getRankingList();
      this.getMyRank();
    },
    /**
     * [getRankingList ]
     * @return {[type]} [description]
     */
    getRankingList(){
      this.$http.get('wx/kneel/ranking?province='+this.province).then((res)=>{
        if(res.body.success){
          this.rankinglist = res.body.data.ranking;
        }
      })
    },
    /**
     * [getMyRank ]
     * @return {[type]} [description]
     */
    getMyRank(){
      this.$http.post('wx/my',{openId:window.OPENID,province:this.province}).then((res)=>{
        if(res.body.success){
          this.myRank = res.body.data.result;
        }
      })
    }
  }
}
</script>

